/* -------------------------------------------------------------------------- */
/*              End of normalize and begin of custom css styling              */
/* -------------------------------------------------------------------------- */

:root {
    --main-bg-color: #fffef6;
    --card-color: #4682A9;
    --light-bg-color: #FFFF;
    --light-color: #91C8E4;
}

html {
    background-color: var(--main-bg-color);
}

/* --------------------------------- Content -------------------------------- */
/* Grid-based system, let footer area and sidebar area sizes be auto sized to content size*/
.content {
    display: grid;
    grid-template-areas: 
    "sidebar content"
    "sidebar footer";
    grid-template-rows: 1fr auto;
    grid-template-columns: auto 1fr;
    min-height: 100vh;
}

/* ----------------------------- Sidebar styling ---------------------------- */
/* Sidebar is flex, since there is no limit on number of projects */
.sidebar {
    grid-area: sidebar;
    display: flex;
    flex-direction: column;
    background-color: var(--light-color);
    align-items: center;
    padding: 0;
    margin: 0;
    min-width: 15vw;
}

.sidebar-header {
    display: flex;
    background-color: var(--card-color);
    color: white;
    border: solid 0.75rem var(--card-color);
    padding-left: 2rem;
}

.siderbar-header h2 {
    text-align: center;
}

.sidebar-top,
.sidebar-bottom,
.sidebar-projects-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem;
    padding-top: 3rem;
    box-sizing: border-box;
}

.sidebar-top {
  padding-bottom: 3rem;
  width: 100%;
}

.sidebar-projects-container {
  padding-top: 1rem;
}

.sidebar-bottom {
    flex: 1;
    width: 100%;
    padding: 0;
    background-color: var(--light-color);
}

ul {
    padding: 0;
    margin: 0;
}

/* Tasks are flex containers so the icons can be aligned to their corresponding text */
.sidebar-util,
.sidebar-project {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem;
    cursor: pointer;
    background-color: var(--card-color);
    border-radius: 1rem;
    color: white;
}

.project-tab {
  display: flex;
  align-items: center;
  background-color: var(--card-color);
  border-radius: 1rem;
  cursor: pointer;
  color: white;
  font-weight: 800;
  border: solid 1px var(--light-bg-color);
  border-left: solid 6px var(--light-bg-color);
  max-height: 10vh;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
}

.project-tab-left {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.project-tab-left svg {
  fill: white;
}

.project-tab:hover,
.sidebar-util:hover {
  filter:brightness(0.85);
}

.sidebar-project-list {
  display: flex;
  height: 100%;
  flex: initial;
  flex-direction: column;
  gap: 0.3rem;
  max-height: 37vh;
  overflow:scroll;
}

/* ------------------------ Main content area styling ----------------------- */

.main-content {
  display: flex;
  width: 100%;
  flex-direction: column;
  grid-area: content;
  padding-bottom: 2rem;
}

.project-container {
  display: flex;
  background-color: var(--card-color);
  color: white;
  font-size: 1.7rem;
  justify-content: center;
}

.tasks-container {
  display: flex;
  flex-direction: column;
  align-self: center;
  width: 50%;
  margin-top: 1rem;
}

.task {
  display: flex;
  flex: 1;
  background-color: var(--light-color);
  border-left: solid 4px var(--card-color);
  border-top: solid 4px var(--card-color);
  color: black;
  align-items: center;
  border-radius: 1rem;
  gap: 1rem;
  justify-content: space-between;
  box-shadow: 1rem 0.5rem 1rem 0 gray;
  padding-left: 2rem;
  padding-right: 2rem;
}

.todo-list {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.task-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.task-utils {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.task:hover {
  filter: brightness(0.98);
}

.inner-check {
  opacity: 0;
  transition: opacity 0.05s;
}

.icon-circle-with-check:hover .inner-check {
  opacity: 1;
}

.todo-button {
  background-color: var(--card-color);
  border-radius: 0.5rem;
  cursor: pointer;
}


/* ----------------------------- Footer styling ----------------------------- */

footer {
    grid-area: footer;
    background-color: var(--main-bg-color);
    color: white;
    padding: 1rem;
    text-align: center;
}

/* ------------------------------ Miscellaneous ----------------------------- */

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
    height: 1.5rem;
    width: 1.5rem;
    flex-shrink: 0;
    fill: white;
}

button {
    border: none;
}

ul {
    list-style: none;
}